import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { changeContactForm } from '../slice'

import styles from './contact.module.scss'

export default function Contact() {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const { name, email, message } = useSelector((state) => state.contactForm)

  function handleChange(e) {
    const {
      target: { name, value },
    } = e
    dispatch(changeContactForm({ name, value }))
  }

  function handleSubmit(e) {
    e.preventDefault()

    // dispatch()    api 

    navigate('/')
  }

  return (
    <form className={styles.form} onSubmit={handleSubmit}>
      <h1>Contact</h1>

      <div className={styles.forminputs}>
        <div className={styles.forminput}>
          <label htmlFor='name'></label>
          <input type='text' id='name' name='name' placeholder='' onChange={handleChange} value={name} />
        </div>

        <div className={styles.forminput}>
          <label htmlFor='email'></label>
          <input type='email' id='email' name='email' placeholder=' ' onChange={handleChange} value={email} />
        </div>
      </div>

      <div className={styles.formText}>
        <label htmlFor='message'></label>
        <textarea
          id='message'
          name='message'
          placeholder=' '
          onChange={handleChange}
          value={message}
        />
      </div>

      <div className={styles.formButton}>
        <button type='submit'></button>
      </div>
    </form>
  )
}
